<template>
  <div class="d-flex">
    <rm-button v-for="(el, k) in list" :key="'rm_btn_' + k" class="mr-2 mb-4" :type="el.type" :size="el.size" :text="el.text" :round="el.round" :showClickedStyle="el.showClickedStyle" @click="onOneClick(k)" />
  </div>
</template>

<script>
const RmButton = () => import("@/components/RmButton");

export default {
  name: "RmButtonGroup",
  components: {
    RmButton
  },
  props: {
    list: {
      type: Array,
      required: true,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      clickedIndex: null
    };
  },
  methods: {
    onOneClick(index) {
      this.$emit('oneClick', index, this.clickedIndex);

      this.clickedIndex = index;
    }
  }
}
</script>